.scrollable {
height: 65vh;
overflow: scroll;
margin-bottom: 10px
}

::-webkit-scrollbar{
    width: 15px;
}

::-webkit-scrollbar-track{
border: 2px solid white;
box-shadow: inset 0 0 2.5px 2px rgba(0,0,0,0.5);
}

::-webkit-scrollbar-thumb{
    background:linear-gradient(
        45deg,
        rgb(117, 142, 165),
        rgb(0, 195, 255)
    );
    border-radius: 3px;
}

